<template>
  <div class="home">
    <div class="container p-3">
      <div class="row">
        <div class="col-6">
          <p class="h1 font-weight-bold text-white">Welcome</p>
        </div>
      </div>
      <div class="row px-3">
        <div class="col-12 inform-card">
          <p class="h3 mb-0 text-white">HI~{{profile.displayName}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import liff from '@line/liff'
export default {
  name: 'HOME',
  data () {
    return {
      profile: {}
    }
  },
  created () {
    this.getprofile()
  },
  methods: {
    getprofile () {
      liff
        .getProfile()
        .then((profile) => {
          this.profile = profile
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.home{
  background: linear-gradient(#ffb4b4,#ffdbc5,#ffffff);
  height: 100vh;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.inform-card{
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 22px;
}
</style>
